@import '../../styles/variables/index.scss';
@import '../../styles/mixins/index.scss';

.Good {
	$Good--border-radius: 16px;

	&--is-loading {
		position: relative;
		&::before {
			content: "";
			position: absolute;
			z-index: 1;
			top: 0;
			right: 0;
			width: 100%;
			height: 100%;
			border-radius: $Good--border-radius;
			background: $color-general--light url(./assests/spinner.gif) center no-repeat;
		}
	}

	&__link {
		@include clickable-element;
		display: block;
		outline: none;
		border-radius: $Good--border-radius;
		background: $color-general--light;
		box-shadow: 0px 5px 9px rgba(81, 29, 15, 0.13);
		transition-duration: 0.2s;

		&:hover,
		&:focus {
			box-shadow: 0px 5px 7px 2px rgba(124, 6, 0, 0.56);
		}
	}

	&__illustration-wrapper {
		width: 100%;
		height: 170px;
	}

	&__image {
		max-width: 100%;
		max-height: 100%;
		object-fit: contain;
		padding-top: 20px;
	}

	&__title {
		$line-height: 21px;

		font-style: normal;
		font-weight: normal;
		font-size: 18px;
		line-height: 21px;

		color: #444444;

		height: 2 * $line-height;
		padding: 0 15px;
		margin-top: 25px;

		overflow: hidden;
	}

	&__info-row {
		display: flex;
		justify-content: space-between;
		margin-top: 7px;
		padding: 0 15px;
		font-style: normal;
		font-weight: 200;
		color: #444444;
	}

	&__producer {
		font-size: 16px;
		line-height: 19px;
	}

	&__portion {
		font-size: 12px;
		line-height: 14px;
	}

	&__price {
		margin-top: 10px;
		padding: 0 15px;
		color: #511d0f;

		font-style: normal;
		font-weight: 600;
	}

	&__price-big {
		font-size: 50px;
		line-height: 60px;
	}

	&__price-small {
		font-size: 14px;
		line-height: 17px;
	}
}
